                                    return Promise.reject(new Error('–î–æ–¥–∞–π—Ç–µ —è–∫ –º—ñ–Ω—ñ–º—É–º –æ–¥–Ω–æ–≥–æ —á–µ—Ä–≥–æ–≤–æ–≥–æ'));
